<p>http://www.gnu.org/</p>
<h2 id="linux-and-gnu-relation" tabindex="-1"><a class="header-anchor" href="#linux-and-gnu-relation" aria-hidden="true">#</a> Linux and Gnu Relation</h2>
<p>GNU是一个操作系统项目, 名字是一个递归的GNU's Not Unix!的缩写.Linux只是一个系统内核，GNU/Linux一起打包。</p>
<p>如果一台用Linux的计算机是一台汽车</p>
<ul>
<li>计算机硬件</li>
</ul>
<p>轮胎，车架，发动机等等的部件</p>
<ul>
<li>Linux</li>
</ul>
<p>传动杆，变速箱，各种传感器和行车电脑，刹车片/卡钳之类的。负责让发动机的能量传到轮胎上，并能获取车辆运行状态的系统部件</p>
<ul>
<li>GNU</li>
</ul>
<p>油门/刹车踏板、挂档杆、方向盘、仪表、车门什么的。让司机能够开和用的部件。</p>
